import {useEffect, useState } from "react";
import axios from 'axios';
import Modal, { setAppElement } from 'react-modal';
import "C:/Users/umada/my-application/src/Styling/Main.scss";
import ManageOptions from "../Production/ManageOptions";
// patch 'undone' plan to 'done'
// final products (see final products, delete them)

function Overview(){

    const url = "https://management-backend-app.herokuapp.com/api/";

    // loaded values
    const [finalProducts, setFinalProducts] = useState([]);
    const [productPlans, setProductPlans] = useState([]);

    // variables

    const [currentPlanId, setCurrentPlanId] = useState(0);
    const [currentQualityAmount, setCurrentQualityAmount] = useState(0);
    const [currentTotalAmount, setCurrentTotalAmount] = useState(0);
    const [currentUnitPrice, setCurrentUnitPrice] = useState(0);

    // panels

    const [productOverviewPanel, setProductOverviewPanel] = useState(true);
    const [profitPanel, setProfitPanel] = useState(false);

    // modals
    const [modalFinalProduct, setModalFinalProduct] = useState(false);

    async function fetchProductPlans(){
        await axios.get(url+'productPlan')
        .then(result => {
            setProductPlans(result.data)
        })
    }

    async function fetchFinalProducts(){
        await axios.get(url+'finalProduct')
        .then(result => { 
            setFinalProducts(result.data);
        })
    }
  
    async function addFinalProduct(){
        axios.post(url+'finalProduct', {
            id: 0,
            productPlanID: currentPlanId,
            qualityAmount: currentQualityAmount,
            totalAmount: currentTotalAmount,
            unitPrice: currentUnitPrice
        }).then(()=> fetchFinalProducts());
    }

    // delete deletes product plan, final product, evaluation and resource.

    function handleProductsPanel(){
        setProductOverviewPanel(true);
        setProfitPanel(false);
    }

    function handleProfitPanel(){
        setProfitPanel(true);
        setProductOverviewPanel(false);
    }

    function handlePatchDonePlan(planId){
        axios.patch(url+"productPlan/"+ planId,{
            done:true
        });
        fetchProductPlans();
        setModalFinalProduct(true);
    }

    function handleAddFinalProduct(e){
        e.preventDefault();
        addFinalProduct();
        setModalFinalProduct(false);
    }
    
    useEffect(() => {
        fetchProductPlans();
        fetchFinalProducts();
    }, []);


    return(

        <div class="main" >
            <div class="selection">

                <button class="selectionButton" onClick = {()=> handleProductsPanel()}>Product overview</button>
               
            </div>

            <div class="display">

                {productOverviewPanel&&(
                    <div class="fullPanel">
                        <div class="halfPanel">

                            <h2>Finalize plans</h2>

                            {productPlans.map(plan => 
                            <ul key={plan.id} onMouseOver={()=> setCurrentPlanId(plan.id)}>
                                {plan.estimatedQuantity} units of {plan.productType} 
                                <button class="changesButton" onClick = {() => handlePatchDonePlan(plan.id)}>Done</button>
                            </ul>
                            )}

                        </div>
                        <div class="halfPanel">
                            <h2>Available products</h2>
                            {finalProducts.map(product => <ul key={product.id}>
                              Plan {product.productPlanID} 
                            </ul>)}
                        </div>
                    </div>
                )}

                {profitPanel&&(

                    <div class="fullPanel">
                        <h2>All plans</h2>
                        {finalProducts.map(product =>
                            <div>
                                <p>Plan : {product.productPlanID}</p>
                                <p>Total good products: {product.qualityAmount}</p>
                                <p>Production efficiency : {parseFloat( parseFloat(product.qualityAmount) / parseFloat(product.totalAmount) ).toFixed(2)} </p>
                                <p>Profit : {parseFloat(product.qualityAmount)*parseFloat(product.unitPrice)} DKK </p>
                            </div>)
                        }

                    </div>
                )}
                
                <div>
                <Modal
                isOpen={modalFinalProduct}
                appElement={document.getElementById('root')}>
                    <h2>Fill the fields to establish a final product</h2>
                    <p>
                    <label>
                        Total Amount
                        <input type = "number" min="0" step={1} onChange={(e) => setCurrentTotalAmount(e.target.value)}></input>   
                    </label>
                    </p>
                    <p>
                    <label>
                        Quality Amount
                        <input type = "number" min="0" step={1} onChange={(e) => setCurrentQualityAmount(e.target.value)}></input>   
                    </label>
                    </p>
                    <p>
                    <label>
                        Unit price
                        <input type = "number" min="0" step={1} onChange={(e) => setCurrentUnitPrice(e.target.value)}></input>   
                    </label>
                    </p>
                    <button class="changesButton" onClick = {(e) => handleAddFinalProduct(e)}>Proceed</button>
                </Modal>
                </div>

            </div>
        </div>
    );
}

export default Overview;