import {useEffect, useState } from "react";
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faTrash } from '@fortawesome/free-solid-svg-icons'
import Modal from 'react-modal';
import "C:/Users/umada/my-application/src/Styling/Main.scss";

function Overview () {
    const url = "https://management-backend-app.herokuapp.com/api/";
    // icons
    const plus = <FontAwesomeIcon icon={faPlus} />

    // Supplier feedback pannel 

    const [supplierFeedbackPanel, showSupplierFeedbackPanel] = useState(false);
    const [resourcePanel, showReasourcePanel] = useState(true);

    // hooks to store resource descriptor api value collections
    const [purposes, setPurposes] = useState([]);
    const [materials, setMaterials] = useState([]);
    const [suppliers, setSuppliers] = useState([]);
    const [resources, setResources] = useState([]);
    const [statuses, setStatuses] = useState([]);
    const [supplierFeedbacks,setSupplierFeedbacks] = useState([]);

    // modal dependencies
    const [modalResources, setModalResources] = useState(false);
    const [modalSupplierFeedback, setModalSupplierFeedback] = useState(false);
    const [modalDateUpdate, setModalDateUpdate] = useState(false);
    const [modalReplaceResource, setModalReplaceResource] = useState(false);

    // hooks for adding a new resource
    const [currentId, setCurrentId] = useState(null);
    const [currentWeight, setCurrentWeight] = useState(1.0);
    const [currentPrice, setCurrentPrice] = useState(1.0);
    const [currentColor, setCurrentColor] = useState("#000000");
    const [currentEstimatedArrival, setCurrentEstimatedArrival] = useState("2021-12-12");
    const [currentStatus,setCurrentStatus] = useState("");
    const [currentMaterial, setCurrentMaterial] = useState("");
    const [currentPurpose,setCurrentPurpose] = useState("");
    const [currentSupplier, setCurrentSupplier] = useState("");

    // hooks for adding a new supplier feedback
    const [currentReasoning,setCurrentReasoning] = useState("");

    // functions to handle modal interactions

    function handleResourceModal(){
        // clearing old modal values
        setModalResources(true);
        setCurrentMaterial(materials[0].title);
        setCurrentPurpose(purposes[0].title);
        setCurrentSupplier(suppliers[0].title);
        setCurrentStatus(statuses[0].status);
    }

    function handleSupplierFeedbackModal(){
        setModalResources(false);
        setModalSupplierFeedback(true);
        setCurrentReasoning(""); // cleaning up the previous value
    }

    function handlePatchDate(id){
        setCurrentId(id);
        setModalDateUpdate(true);
    }

    function handlePutReplaceResource(id, weight, material, color, purpose){
        setCurrentId(id);
        setCurrentWeight(weight);
        setCurrentMaterial(material);
        setCurrentColor(color);
        setCurrentPurpose(purpose);
        setModalReplaceResource(true);
    }

    // functions that manage appearance when buttons are clicked.

    function handleSupplierFeedbackPanel(){
        showReasourcePanel(false)
        showSupplierFeedbackPanel(true)
    }

    function handleResourcePanel(){
        showSupplierFeedbackPanel(false);
        showReasourcePanel(true)
    }

// internal functions 
    useEffect(() => {
        fetchPurpose();
        fetchSupplier();
        fetchMaterial();
        fetchStatuses();
        fetchResources();
        fetchSupplierFeedbacks();
    }, []);


// fetching data functions

    async function fetchPurpose(){
        await axios.get(url+'resourcePurpose')
        .then(result => { 
            setPurposes(result.data);
    })}

    async function fetchMaterial(){
        await axios.get(url+'resourceMaterial')
        .then(result => { 
            setMaterials(result.data);
    })}
    
    async function fetchSupplier(){
        await axios.get(url+'resourceSupplier')
        .then(result => { 
            setSuppliers(result.data);
    })}

    async function fetchResources(){
        await axios.get(url+'textileResources')
        .then(result => { 
            setResources(result.data);
    })}

    async function fetchStatuses(){
        await axios.get(url+'resourceStatus')
        .then(result => { 
            setStatuses(result.data);
    })}

    async function fetchSupplierFeedbacks(){
        await axios.get(url+"supplierFeedback")
        .then(result =>
            setSupplierFeedbacks(result.data));
    }

    // current date expression 
    const today = new Date();
    const month = ((today.getMonth() +1) <10 ? "0" + String(today.getMonth() + 1) : String(today.getMonth()+1));
    const day = (today.getDate()) < 10  ? "0" + String(today.getDate()) : String(today.getDate());
    const currentDate =   String(today.getFullYear())+ "-" + month+ "-" + day;

    // functions to add new properties
    function addResource(){
        axios.post(url+'textileResources',{
        color: currentColor,
        estimatedArrival: currentEstimatedArrival,
        id: 0,
        material: currentMaterial,
        price: currentPrice,
        purpose: currentPurpose,
        registryDate: currentDate,
        status: currentStatus,
        supplier: currentSupplier,
        weight: currentWeight
    }).then(()=> fetchResources());
    }

    function handleAddResource(e){
        e.preventDefault();
        addResource();
        handleSupplierFeedbackModal();
    }

    function addSupplierFeedback(){
        axios.post(url+'supplierFeedback', {
            id:0,
            resourceID: resources.length,
            supplier: currentSupplier,
            purchaseReasoning: currentReasoning,
            qualityReasoning: ""
        }).then(()=> fetchSupplierFeedbacks());
    }

    function handleAddSupplierFeedback(e){
        e.preventDefault();
        addSupplierFeedback();
        setModalSupplierFeedback(false);
    }

    function patchDate(){
        axios.patch(url+'textileResources/'+ currentId,{
            estimatedArrival: currentEstimatedArrival
        })
        fetchResources();
    }

    function handlePatchDate(e){
        e.preventDefault();
        patchDate();
        setModalDateUpdate(false);
    }

    function putReplaceResource(){
        axios.put(url+'textileResources/'+ currentId,{
            estimatedArrival: currentEstimatedArrival,
            registryDate: currentDate,
            material: currentMaterial,
            price: currentPrice,
            status: statuses[0].status,
            supplier: currentSupplier,
            purpose: currentPurpose,
            weight: currentWeight,
            color: currentColor
        }).then(() => fetchResources());

    }

    function handlePutReplaceResource(e){
        e.preventDefault();
        putReplaceResource();
        setModalReplaceResource(false);
    }

    return(
        
        <div class="main">

            <div class="selection">
            <button class="selectionButton" onClick={() => handleResourceModal()}>{plus}</button>
            <button class="selectionButton" onClick= {() => handleResourcePanel()}>See resources</button>
            <button class="selectionButton" onClick= {() => handleSupplierFeedbackPanel()}>Supplier feedback</button>
            </div>

            <div class = "display">

            {resourcePanel&&(

                <div class = "filler">
                <div class="panel">
                <h2>
                    To be evaluated
                </h2>
                <div class="inner">
                    {resources.map(resource => (resource.status == "To Evaluate" ? 
                    <ul>{resource.weight} kg of {resource.material} for {resource.purpose} due on {resource.estimatedArrival}
                    
                    {resource.estimatedArrival<currentDate?
                    <button class="changesButton" onClick={()=> handlePatchDate(resource.id)}>Change date</button>
                    :
                    null
                    }
                    </ul>
                    : null)
                    )}
                </div>
            </div>

            <div class="panel">
                <h2>
                    Returned
                </h2>
                <div class="inner">
                {resources.map(resource => (resource.status == "Returned" ? 
                    <ul>{resource.weight} kg of {resource.material} for {resource.purpose}
                    <button class="changesButton" onClick= {() => handlePutReplaceResource(resource.id, resource.weight,
                         resource.material, resource.color, resource.purpose)}>
                        Replace</button>
                    </ul>
                    : null)
                    )}
                </div>
            </div>
            
            <div class="panel" id="end">
                <h2>
                    Accepted
                </h2>
                <div class="inner">
                {resources.map(resource => (resource.status== "Accepted" ? 
                    <ul>{resource.material} for {resource.purpose}
                    </ul>
                    : null)
                    )}
                </div>
            </div>
            </div>
            )}

            {supplierFeedbackPanel&&(

                <div>
                    <h2>See supplier feedback</h2>
                    {supplierFeedbacks.map(feedback =>
                        <div>
                        <p>Supplier: {feedback.supplier} </p>
                        <p>for resource {feedback.resourceID}</p>
                        <p>Quality: {feedback.qualityReasoning} </p>
                        <p>Communication: {feedback.purchaseReasoning}</p>
                        </div>
                    )}
                </div>
            )}

            {/* Modal for supplier feedback*/}

            <div>
                <Modal
                isOpen={modalSupplierFeedback}
                appElement={document.getElementById('root')}>
                    <h2>Add feedback to describe supplier's communication</h2>
                    <p>
                    <input type="text" onChange={(e)=> setCurrentReasoning(e.target.value)}></input>
                    </p>
                    <button class="changesButton" onClick = {(e) => handleAddSupplierFeedback(e)}>Complete</button>
                </Modal>
            </div>

            {/* Modal for changing the date of a resource*/}

            <div>
                <Modal
                isOpen={modalDateUpdate}
                appElement={document.getElementById('root')}>
                    <h2>Set a new date</h2>
                    <label>
                            Estimated Arrival date
                            <input type = "date" onChange={(e) => setCurrentEstimatedArrival(e.target.value)}></input>    
                    </label>
                    <button class="changesButton" onClick = {(e) => handlePatchDate(e)}>Set</button>
                
                </Modal>
            </div>

            {/* Modal for replacing returned resources*/}

            <div>
                <Modal
                isOpen={modalReplaceResource}
                appElement={document.getElementById('root')}>
                    <h2>Replace a returned resource</h2>
                    <div class="inner">
                    <p>
                    <label>
                            Suppliers
                            <select onChange={(e) => setCurrentSupplier(e.target.value)}>
                            {suppliers.map( option => 
                            <option key={option.id} value={option.title}>{option.title}</option>)}
                            </select>
                    </label>
                    </p>    
                    <p>
                    <label>
                            Estimated Arrival date
                            <input type = "date" onChange={(e) => setCurrentEstimatedArrival(e.target.value)}></input>    
                    </label>
                    </p>
                    <p>
                    <label>
                        Price
                        <input type = "number" min="0" step={10} precision={0.1} onChange={(e) => setCurrentPrice(e.target.value)}></input>   
                    </label>
                    </p>
                    <button class="changesButton" onClick={(e) => putReplaceResource(e)}>Replace</button>

                </div>

                </Modal>
            </div>


            {/* Modal for adding resources*/}
            <div> {/* Resources modal*/}
                <Modal
                isOpen={modalResources}
                appElement={document.getElementById('root')}>
                    <h2>New Resource</h2>
                    <button class="changesButton" onClick={() => setModalResources(false)}> Close </button>

                    <p>
                        <label>
                            Weight
                            <input type = "number" min="0" step={10} precision={0.1} onChange={(e) => setCurrentWeight(e.target.value)}></input>
                        </label>
                    </p>    
                    <p>
                        <label>
                            Price
                            <input type = "number" min="0" step={10} precision={0.1} onChange={(e) => setCurrentPrice(e.target.value)}></input>   
                        </label>
                    </p>    
                    <p>
                        <label>
                            Color
                            <input type = "color" onChange={(e) => setCurrentColor(e.target.value)}></input>
                        </label>
                    </p>
                    <p>
                        <label>
                            Estimated Arrival date
                            <input type = "date" onChange={(e) => setCurrentEstimatedArrival(e.target.value)}></input>    
                        </label>
                    </p>
                    <p>
                        <label>
                            Material
                            <select onChange={(e) => setCurrentMaterial(e.target.value)}>
                            {materials.map( option => 
                            <option key={option.id} value={option.title}>{option.title}</option>)}
                            </select>
                        </label>
                    </p>    
                    <p>
                        <label>
                            Purposes
                            <select onChange={(e) => setCurrentPurpose(e.target.value)}>
                            {purposes.map( option => 
                            <option key={option.id} value={option.title}>{option.title}</option>)}
                            </select>
                        </label>
                    </p>   
                    <p>
                        <label>
                            Suppliers
                            <select onChange={(e) => setCurrentSupplier(e.target.value)}>
                            {suppliers.map( option => 
                            <option key={option.id} value={option.title}>{option.title}</option>)}
                            </select>
                        </label>
                    </p>

                        <button class="changesButton" onClick = {e => handleAddResource(e)}>Proceed</button>
                </Modal>
            </div>
            </div>
        </div>
    )
}

export default Overview;