

import {useEffect, useState } from "react";
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faTrash } from '@fortawesome/free-solid-svg-icons'
import Modal from 'react-modal';
import "C:/Users/umada/my-application/src/Styling/Main.scss";

function Overview(){

    const url = "https://management-backend-app.herokuapp.com/";

    // current date expression 
    const today = new Date();
    const month = ((today.getMonth() +1) <10 ? "0" + String(today.getMonth() + 1) : String(today.getMonth()+1));
    const day = (today.getDate()) < 10  ? "0" + String(today.getDate()) : String(today.getDate());
    const currentDate =   String(today.getFullYear())+ "-" + month+ "-" + day;

    // panels

    const [showResourceEvaluation, setShowResourceEvaluation] = useState(true);
    const [showProductPlans, setShowProductPlans] = useState(false);

    // modal dependencies

    const [modalResourceEvaluation, setModalEvaluation] = useState(false);
    const [modalProductPlan, setModalProductPlan] = useState(false);
    const [modalSupplierFeedback, setModalSupplierFeedback] = useState(false);
    const [modalReceive,setModalReceive] = useState(false);
    const [modalDateUpdate, setModalDateUpdate] = useState(false);

    // fetched lists

    const [suppliers, setSuppliers] = useState([]);
    const [resourceEvaluations, setResourceEvaluations] = useState([]);
    const [productPlans, setProductPlans] = useState([]);
    const [productTypes, setProductTypes] = useState([]);
    const [resources, setResources] = useState([]);
    const [statuses, setStatuses] = useState([]);


    // hooks used for establishing and using states of data.
    
    const [currentId, setCurrentId] = useState(null);
    const [currentWeight, setCurrentWeight] = useState(1.0);
    const [currentPrice, setCurrentPrice] = useState(1.0);
    const [currentColor, setCurrentColor] = useState("#000000");
    const [currentEstimatedArrival, setCurrentEstimatedArrival] = useState("2021-12-12");
    const [currentRegistryDate, setCurrentRegistryDate] = useState("2021-12-12");
    const [currentMaterial, setCurrentMaterial] = useState("");
    const [currentPurpose,setCurrentPurpose] = useState("");
    const [currentSupplier, setCurrentSupplier] = useState("");
    const [currentResourceID, setCurrentResourceID] = useState(1);
    const [currentProductType, setCurrentProductType] = useState();
    const [currentEstimatedQuantity, setCurrentEstimatedQuantity] = useState(1);
    const [currentUsedWeight, setCurrentUsedWeight] = useState(1.0);
    const [currentQualityReasoning, setCurrentQualityReasoning] = useState("");

    useEffect(() => {
        fetchSuppliers();
        fetchStatuses();
        fetchResources();
        fetchProductTypes();
        fetchProductPlans();
        fetchResourceEvaluations();
    }, []);

    async function fetchSuppliers(){
        await axios.get(url+'api/resourceSupplier')
        .then(result => { 
            setSuppliers(result.data);
    })}

    async function fetchStatuses(){
        await axios.get(url+'api/resourceStatus')
        .then(result => { 
            setStatuses(result.data);
    })}

    // fetch product types
    async function fetchProductTypes(){
        await axios.get(url+'api/productType')
        .then(result => { 
            setProductTypes(result.data);
        })
    }

    async function fetchResources(){
        await axios.get(url+'api/textileResources')
        .then(result => { 
            setResources(result.data);
    })}

    // fetch evaluations
    async function fetchResourceEvaluations(){
        await axios.get(url+'api/evaluated')
        .then(result => { 
            setResourceEvaluations(result.data);
        })
    }

    function handlePatchDate(id){
        setCurrentId(id);
        setModalDateUpdate(true);
    }

    function manageProductPlanCompletion(id){
        axios.patch(url+'api/productPlan/'+id +'/'+ true, { // change last argument
            done: true
        }).catch((error) => console.log('Error: ', error))
    }

    // fetch product profiles
    async function fetchProductPlans(){
        await axios.get(url+'api/productPlan')
        .then(result => { 
            setProductPlans(result.data);
        });
    }

    function addResourceEvaluation(){
        axios.post(url+'api/evaluated',{
            id: 0,
		    resourceID : currentId,
		    receivedAt: currentDate,
            assignedToPlan:false
        }).then(()=> fetchResourceEvaluations())
        .then(() => fetchResources());
    }

    // add product profile

    function addProductPlan(e){
        e.preventDefault();
        axios.post(url+'api/productPlan',{
            id: 0,
            done: false,
            productType: currentProductType,
            estimatedQuantity: currentEstimatedQuantity,
            estimatedArrival: currentEstimatedArrival,
            usedWeight: currentUsedWeight,
            resourceID: currentResourceID
        }).then(()=> fetchProductPlans());

        setModalProductPlan(false);
    }

    function handleEvaluations(){
        setShowProductPlans(false);
        setShowResourceEvaluation(true);
    }

    function handleProjectPlans(){
        setShowResourceEvaluation(false);
        setShowProductPlans(true);
    }

    function handleModalReceive(id,estimatedArrival, weight, material, color, purpose, price,
        registryDate, supplier){
        
        setCurrentResourceID(id);
        setCurrentId(id);
        setCurrentWeight(weight);
        setCurrentMaterial(material);
        setCurrentColor(color);
        setCurrentPurpose(purpose);
        setCurrentSupplier(supplier);
        setCurrentPrice(price);
        setCurrentEstimatedArrival(estimatedArrival);
        setCurrentRegistryDate(registryDate);

        setModalReceive(true);
    }

    function handleModalProductPlan(evaluationId){
        patchEvaluationAssignedToPlan(evaluationId);
        setModalProductPlan(true);
    }

    function handleReturned(){
        setModalReceive(false);
        putStatus(statuses[2].status);
        fetchResources();
        setModalSupplierFeedback(true);
        
    }

    function handleAccepted(){
        setModalReceive(false);
        putStatus(statuses[1].status);
        setModalEvaluation(true);
        // use put to change the specified resource to "Accepted" in status
        // open Evaluation modal and add new evaluation.
    }

    function handleAddResourceEvaluation(e){
        e.preventDefault();
        addResourceEvaluation();
        patchQualityReasoning();
        setModalEvaluation(false);
    }

    function handleJustSupplierFeedback(e){
        e.preventDefault();
        patchQualityReasoning();
        setModalSupplierFeedback(false);
    }


    function putStatus(selectedStatus){

        axios.put(url + 'api/textileResources/'+ currentId,{
            estimatedArrival: currentEstimatedArrival,
            registryDate: currentRegistryDate,
            material: currentMaterial,
            price: currentPrice,
            status: selectedStatus,
            supplier: currentSupplier,
            purpose: currentPurpose,
            weight: currentWeight,
            color: currentColor
        });
        fetchResources();
    }

    function GetOneCategory(props){
        axios.get(url + 'api/productCategory/'+props.cat, {
        }).then(result => {return(JSON.stringify(result.data.title))});
    }

    function patchQualityReasoning(){
        axios.patch(url+ "api/supplierFeedback/"+ currentResourceID,{
            qualityReasoning: currentQualityReasoning
        });
    }

    function patchEvaluationAssignedToPlan(evaluationId){

        axios.patch(url+ "api/evaluated/"+ evaluationId,{
            assignedToPlan: true
        });

        fetchResourceEvaluations();
        fetchProductPlans();
    }

    function patchDate(e){
        e.preventDefault();
        axios.patch(url+'api/productPlan/'+ currentId,{
            estimatedArrival: currentEstimatedArrival
        });
        fetchProductPlans();
        setModalDateUpdate(false);
    }


    return(
        <div class="main">
            {/**/}
            <div class = "selection">
            <button class="selectionButton" onClick={() => handleEvaluations()}>Manage Evaluations</button>
            <button class="selectionButton" onClick={() => handleProjectPlans()}>Manage Product planning</button>
            </div>

            <div class= "display">
            {/*show resourceEvaluations (show resources with 'to evaluate' status)*/}
            {showResourceEvaluation ? 
            <div class = "fullPanel">
                <div class = "halfPanel">
                <h2>The following resources are waiting for evaluation: </h2>
                {resources.map(resource => (resource.status == "To Evaluate" ? 
                    <ul key={resource.id} onMouseOver={()=> setCurrentResourceID(resource.id)}>
                    <button class="changesButton" onClick={() => 
                    handleModalReceive(resource.id,resource.estimatedArrival,resource.weight,resource.material,
                        resource.color, resource.purpose, resource.price, resource.registryDate, 
                        resource.supplier)}>Evaluate</button>
                    {resource.weight} kg of {resource.material}
                    </ul>
                    : null)
                )}
                </div>

                <div class = "halfPanel" id="end">
                <h2>Current evaluations</h2>
                {resourceEvaluations.map(evaluation =>
                    <ul key={evaluation.id}> {evaluation.receivedAt} 
                    </ul>
                    )}
                </div>
            </div>
            
            :
            null}

            {/*show product plans*/}

            {showProductPlans ? 
                <div class = "fullPanel">
                <div class = "halfPanel">
                <h2>Assign evaluated resources to product plans</h2>


                {resourceEvaluations.map(evaluation => (evaluation.assignedToPlan ? 
                    null
                    :
                    <ul key={evaluation.id} onMouseOver={()=> setCurrentResourceID(evaluation.resourceID)}>
                        Evaluation {evaluation.resourceID} ({evaluation.evaluationReasoning})
                    <button class="changesButton" onClick={() => handleModalProductPlan(evaluation.id)}>Assign</button>
                    </ul>
                    )
                    )}
                </div>
                <div class = "halfPanel" id="end">

                <h2>Current product plans</h2>

                {productPlans.map(plan =>
                    <ul key={plan.id} onMouseOver={()=> setCurrentResourceID(plan.id)}>
                        {plan.estimatedQuantity} of {plan.productType} due on {plan.estimatedArrival}

                        {plan.estimatedArrival<currentDate?
                        <button class="changesButton" onClick={()=> handlePatchDate(plan.id)}>Update arrival date</button>
                        :
                        null
                        }

                        {plan.done ?
                        <b> completed</b>
                        :
                        <button class="changesButton" onClick={() => manageProductPlanCompletion()}> Complete </button>}
                    </ul>
                    )}
                </div>
                </div>
            
            :
            null}
            
            <div>
                <Modal
                isOpen={modalSupplierFeedback}
                appElement={document.getElementById('root')}>
                    <h2>Why did you return the resource?</h2>
                    <button class="changesButton" onClick={() => setModalSupplierFeedback(false)}> Close </button>
                    <label>
                        Describe the quality
                        <input type="text" onChange={(e) => setCurrentQualityReasoning(e.target.value)}></input>
                    </label>
                    <button class="changesButton" onClick = {e => handleJustSupplierFeedback(e)}>Proceed to evaluate</button>
                </Modal>
            </div>

            {/*Modal to add new resource Evaluation*/}

            <div>
                <Modal
                isOpen={modalResourceEvaluation}
                appElement={document.getElementById('root')}>
                    <h2>New Evaluation</h2>
                    <label>
                        Describe the quality
                        <input type="text" onChange={(e) => setCurrentQualityReasoning(e.target.value)}></input>
                    </label>
                    <button class="changesButton" onClick = {e => handleAddResourceEvaluation(e)}>Proceed to evaluate</button>
                </Modal>
            </div>

            {/*Modal to add new product plan (show resources with 'accepted' status)*/}
            
            <div>
                <Modal
                isOpen={modalProductPlan}
                appElement={document.getElementById('root')}>
                    <h2>New Product Plan</h2>
                    
                    <label>
                            Product type
                            <select onChange={(e) => setCurrentProductType(e.target.value)}>
                            {productTypes.map( option => 
                            <option key={option.id} value={option.title}> {option.title} 
                            <GetOneCategory cat={option.productCategoryID} /> </option>)}
                            </select>
                    </label>
                    
                    <label>
                            Estimated Arrival date
                            <input type = "date" onChange={(e) => setCurrentEstimatedArrival(e.target.value)}></input>    
                    </label>

                    <label>
                        EstimatedQuantity
                        <input type = "number" min="0" step={10} onChange={(e) => setCurrentEstimatedQuantity(e.target.value)}></input>   
                    </label>

                    <label>
                        Used Weight
                        <input type = "number" min="0" step={5} onChange={(e) => setCurrentUsedWeight(e.target.value)}></input>   
                    </label>

                    <button class="changesButton" onClick = {e => addProductPlan(e)}>Add a new Product plan</button>
                </Modal>
            </div>

            <div>
                <Modal
                isOpen={modalReceive}
                appElement={document.getElementById('root')}>
                    <button class="changesButton" onClick = {() => setModalReceive(false)}>Close</button>
                    <h2>Accept or decline the product</h2>
                    <div>
                    <button class="changesButton" onClick = {() => handleAccepted()}>Accept</button>
                    <button class="changesButton" onClick = {() => handleReturned()} >Return</button>
                    </div>
                </Modal>
            </div>

            <div>
                <Modal
                isOpen={modalDateUpdate}
                appElement={document.getElementById('root')}>
                    <h2>Set a new date</h2>
                    <label>
                            Estimated Arrival date
                            <input type = "date" onChange={(e) => setCurrentEstimatedArrival(e.target.value)}></input>    
                    </label>
                    <button class="changesButton" onClick = {(e) => patchDate(e)}>Set</button>
                
                </Modal>
            </div>

            </div>
        </div>
    )
}

export default Overview;